/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Prince","date":"23 Apr 2016","slug":"/2016-04-23-prince/","tags":"Y2016"},"html":"<p>I was dreamin' when I wrote this, forgive me if it goes astray…</p>\n<p>When I lived in Minneapolis, I used to think we were cooler than everyone else because we had our own resident musical genius. That's not true of course, but he <a href=\"http://www.startribune.com/prince-was-one-of-our-own-and-he-made-minnesota-seem-cool/376798251/\">made us seem cool</a>. And even after he was rich and famous, he stayed in Minnesota. Most of the time, anyway.</p>\n<p>And now in Europe, even before last week I could say I'm from Minnesota \"where Prince is from\", and for a few moments I'm hip and cool. At least until we change the subject.</p>\n<p>And maybe, even though I was just a casual fan, that's why his death has saddened me. I've lost a piece of home. <em>Ich habe ein Stück Heimat verloren.</em></p>\n<p>To this day, I keep a copy of Dirty Mind (1980) on my current mobile device. It was my first Prince record. It's an imperfect album. It's sparsely produced, it almost sounds like a demo. And the lyrics, well, Prince was the artist for whom the term \"explicit content\" was invented, and they distract from the music. But it's tight. Every note, every beat, on every track hits. Just listen to the 2nd side (this was back when albums had sides), with Uptown-Head-Sister-Partyup, beginning to end. It still blows me away.</p>\n<p>(My favorite track, though, is on the first side, <a href=\"https://en.wikipedia.org/wiki/When_You_Were_Mine_(Prince_song)\">When You Were Mine</a>. Mr. LoveSexy turns into a bat-shit-crazy ex. Both funny and sexy as hell.)</p>"},"next":{"fields":{"title":"The Ants","date":"18 Apr 2016","slug":"/2016-04-18-the-ants/"}},"previous":{"fields":{"title":"How to build 3000 pages in Jekyll in less than 4 seconds","date":"25 Apr 2016","slug":"/2016-04-25-how-to-build-3000-pages-in-jekyll-in-less-than-4-seconds/"}}},{"node":{"fields":{"title":"The Ants","date":"18 Apr 2016","slug":"/2016-04-18-the-ants/","tags":"Y2016"},"html":"<p>Our house turns 20 years old this year. And every spring the same thing happens. The ants come into the kitchen for a few weeks. Then they disappear.  </p>\n<p>Ours is a good, solid German house, made of brick. But we live out in the country, and out in the country one has to deal with critters and creepy-crawly things. The doors and windows are tight, but there is obviously a crack in the outside wall to the kitchen. Every spring the ants wake up, get hungry, come inside the wall, and then maybe behind the cabinets, and find an entry to the kitchen.</p>\n<p>Every year they find a <em>new</em> entry. I've heard that ants have collective intelligence, and that it's probably passed down from generation to generation. They are probably more intelligent than we, since every year we forget that the ants are coming. And then it takes us several days to find their point of entry.</p>\n<p>Since we can't think like ants, we have to wait until a whole bunch of them have come in so we can see their trail. Then we get out the ant spray, secure the entry, and wait a few days see if they find a new entry and come back.</p>\n<p>They usually don't. The spring weather gets warmer, and I presume the ants then find it easier to find their food outside rather than risk encountering ant spray inside. But they have the knowledge of how to get into our kitchen, and their offspring will use that knowledge next year, to find a new entry.</p>\n<p>Here is this year's point of entry, and our weapon of choice. We're planning on renovating our kitchen this year. Maybe we'll find that crack in the wall and get it patched.</p>\n<p><img src=\"/assets/ants-180416.jpg\" alt=\"The Ants\"></p>"},"next":{"fields":{"title":"Student Exchange","date":"05 Apr 2016","slug":"/2016-04-05-student-exchange/"}},"previous":{"fields":{"title":"Prince","date":"23 Apr 2016","slug":"/2016-04-23-prince/"}}},{"node":{"fields":{"title":"Student Exchange","date":"05 Apr 2016","slug":"/2016-04-05-student-exchange/","tags":"Y2016"},"html":"<p>We have an exchange student from Italy with us this week (his family hosted our son for a week last fall). We were a bit worried about how to keep him occupied, since we're out in the country and far from the school. We needn't have worried. The students (hosts and guests) hang out in Lüneburg after school and they only come home to sleep.</p>\n<p>BTW, German is cool in that it was a different word for grade and high school students <em>(Schüler)</em> than for college students <em>(Studenten)</em>. We're talking about <em>Schüler</em> here. And that my wife and I met when she was an <a href=\"http://www.papascott.de/archives/2009/11/17/and-i-saw-her-standing-there/\">exchange student</a> <em>(Schüler)</em> in Minnesota is totally beside the point!</p>"},"next":{"fields":{"title":"Gliding","date":"05 Apr 2016","slug":"/2016-04-05-gliding/"}},"previous":{"fields":{"title":"The Ants","date":"18 Apr 2016","slug":"/2016-04-18-the-ants/"}}}],"pathPrefix":"","first":false,"last":false,"index":8,"pageCount":1101}}

/***/ })

